import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const Shopcart = () =>
    import("../views/shopcart/shopcart");
const Home = () =>
    import("../views/home/home");
const Category = () =>
    import("../views/category/category");
const Profile = () =>
    import("../views/profile/profile");
const DepartmentList = () =>
    import("../views/department/department");
const Departlist = () =>
    import("../views/department/departlist");
const ComfireReser = () =>
    import("../views/department/comfirereservsion");
const OrderConfirm = () =>
    import("../views/department/orderpay");
const NoPatientInfo = () =>
    import("../views/department/nopatientinfo");
const AddPatientInfo = () =>
    import("../views/department/addinfopatient");
// 新增预约门诊
const DepartmentOPC = () =>
    import("../views/Appointment/departmentOPC");

// 门诊缴费
const OutpatientPayment = () =>
    import("../views/Outpatient/outpatientPayment");
const OutpatientTopay = () =>
    import("../views/Outpatient/outpatientTopay");
const OutpatientRecord = () =>
    import("../views/Outpatient/outpatientRecord");
const OutpatientAlreadypay = () =>
    import("../views/Outpatient/outpatientAlreadypay");
// 预交金充值
const Themakenone = () =>
    import("../views/Prepay/themakenone");
const Themakenbound = () =>
    import("../views/Prepay/themakenbound");
const Myhospital = () =>
    import("../views/Prepay/myhospital");
const Prepayment = () =>
    import("../views/Prepay/prepayment");
const Perpayrecord = () =>
    import("../views/Prepay/perpayrecord");
const Prepaybill = () =>
    import("../views/Prepay/prepaybill");
const DailylistingIn = () =>
    import("../views/Prepay/dailylistingIn");
// 住院日清单
const InhospitalList = () =>
    import("../views/detailedList/inhospitalList");

const routes = [{
    path: "",
    redirect: "/shopcart",
    meta: {
        title: "鹰潭市中医院"
    }
},
{
    path: "/home",
    component: Home,
    meta: {
        title: '鹰潭市中医院'
    }
},
{
    path: "/category",
    component: Category,
    meta: {
        title: '鹰潭市中医院'
    }
},
{
    path: "/shopcart",
    component: Shopcart
},
{
    path: "/profile",
    component: Profile
},
{
    name: "department",
    path: "/shopcart/department",
    component: DepartmentList
},
{
    name: "departlist",
    path: "/shopcart/department/departlist",
    component: Departlist
},
{
    name: "comfirereservsion",
    path: "/shopcart/department/comfirereservsion",
    component: ComfireReser
},
{
    name: "orderpay",
    path: "/shopcart/department/orderpay",
    component: OrderConfirm
},
{
    name: "nopatientinfo",
    path: "/shopcart/department/nopatientinfo",
    component: NoPatientInfo
},
{
    name: "addpatientinfo",
    path: "/shopcart/department/addinfopatient",
    component: AddPatientInfo
},

// 新增预约门诊
{
    name: "departmentOPC",
    path: "/shopcart/Appointment/departmentOPC",
    component: DepartmentOPC
},
// 门诊缴费
{
    name: "OutpatientPayment",
    path: "/shopcart/Outpatient/outpatientPayment",
    component: OutpatientPayment
},
{
    name: "OutpatientTopay",
    path: "/shopcart/Outpatient/outpatientTopay",
    component: OutpatientTopay
}, {
    name: "OutpatientRecord",
    path: "/shopcart/Outpatient/outpatientRecord",
    component: OutpatientRecord,
    meta: {
        title: '11111'
    }
},
{
    name: "OutpatientAlreadypay",
    path: "/shopcart/Outpatient/outpatientAlreadypay",
    component: OutpatientAlreadypay,
    meta: {
        title: '已缴费详情'
    }
},
// 预交金充值
{
    name: "themakenone",
    path: "/shopcart/Prepay/themakenone",
    component: Themakenone,
    meta: {
        title: "住院号未绑定"
    }
},
{
    name: "themakenbound",
    path: "/shopcart/Prepay/themakenbound",
    component: Themakenbound,
    meta: {
        title: "绑定住院号"
    }
},
{
    name: "myhospital",
    path: "/shopcart/Prepay/myhospital",
    component: Myhospital,
    meta: {
        title: "我的住院号"
    }
},
{
    name: "prepayment",
    path: "/shopcart/Prepay/prepayment",
    component: Prepayment,
    meta: {
        title: "预约金充值"
    }
},
{
    name: "perpayrecord",
    path: "/shopcart/Prepay/perpayrecord",
    component: Perpayrecord,
    meta: {
        title: "预约金充值记录"

    }
},
{
    name: "prepaybill",
    path: "/shopcart/Prepay/prepaybill",
    component: Prepaybill,
    meta: {
        title: "账单详情"
    }
},
{
    name: "dailylistingIn",
    path: "/shopcart/Prepay/dailylistingIn",
    component: DailylistingIn,
    meta: {
        title: ""
    }
},
// 住院日清单
{
    name: "inhospitalList",
    path: "/shopcart/detailedList/inhospitalList",
    component: InhospitalList,
    meta: {
        title: ""
    }
},
];

const router = new VueRouter({
    routes,
    mode: "history"
});

// 

export default router;


